import React from "react";
import { Table,Container,Button,ButtonGroup,OverlayTrigger,Popover,InputGroup, FormControl} from 'react-bootstrap';
import "./my.css"
import {APIGET} from "./apisrv";
var dateDiff = function (timestamp) {
    if (timestamp==0){
        return ""
    }
    // 补全为13位
    var arrTimestamp = (timestamp + '').split('');
    for (var start = 0; start < 13; start++) {
        if (!arrTimestamp[start]) {
            arrTimestamp[start] = '0';
        }
    }
    timestamp = arrTimestamp.join('') * 1;

    var minute = 1000 * 60;
    var hour = minute * 60;
    var day = hour * 24;
    var halfamonth = day * 15;
    var month = day * 30;
    var now = new Date().getTime();
    var diffValue = now - timestamp;

    // 如果本地时间反而小于变量时间
    if (diffValue < 0) {
        return '不久前';
    }

    // 计算差异时间的量级
    var monthC = diffValue / month;
    var weekC = diffValue / (7 * day);
    var dayC = diffValue / day;
    var hourC = diffValue / hour;
    var minC = diffValue / minute;

    // 数值补0方法
    var zero = function (value) {
        if (value < 10) {
            return '0' + value;
        }
        return value;
    };

    // 使用
    if (monthC > 12) {
        // 超过1年，直接显示年月日
        return (function () {
            var date = new Date(timestamp);
            return date.getFullYear() + '年' + zero(date.getMonth() + 1) + '月' + zero(date.getDate()) + '日';
        })();
    } else if (monthC >= 1) {
        return parseInt(monthC) + "月前";
    } else if (weekC >= 1) {
        return parseInt(weekC) + "周前";
    } else if (dayC >= 1) {
        return parseInt(dayC) + "天前";
    } else if (hourC >= 1) {
        return parseInt(hourC) + "小时前";
    } else if (minC >= 1) {
        return parseInt(minC) + "分钟前";
    }
    return '刚刚';
};


function showWalletInfo(index,waddr,mark) {
    let _waddr = waddr.substr(0,4)+"..."+waddr.substr(38,4)
    return <OverlayTrigger
        rootClose
        trigger="click"
        key={waddr}
        placement="right"
        overlay={
            <Popover id={`popover-positioned-${waddr}`}>
                <Popover.Header as="h6">{waddr}</Popover.Header>
                <Popover.Body>
                    <InputGroup className="mb-3">
                        <FormControl
                            placeholder="修改备注"
                            aria-label="修改备注"
                            aria-describedby="basic-addon2"
                            id={`mark-`+waddr}
                        />
                        <Button variant="outline-secondary" onClick={()=>ChangeMark(index,waddr,document.getElementById("mark-"+waddr).value)}>
                            修改
                        </Button>
                    </InputGroup>
                </Popover.Body>
            </Popover>
        }
    >
        <Button variant="outline-secondary">{mark.length!=0&&<h4 className="text-dark">{mark}</h4>}<small>{_waddr}</small></Button>
    </OverlayTrigger>
}

function showFeeInfo(fee,Last_exchange) {
    if (Last_exchange==0){
        if (fee==0){
            return <h4>-</h4>
        }
        return <h4>{fee}%</h4>
    }
    return <Container><h4>{fee}%</h4><h6>{dateDiff(Last_exchange)}</h6></Container>
}

function showWalletBalance(Lus,Bnb) {
    return <Container><h4>{Lus.toFixed(2)} lus</h4><h6>{Bnb.toFixed(2)} bnb</h6></Container>
}

function showGameBalance(Lus,All) {
    return <Container><h4>{Lus.toFixed(2)} mlus</h4><h6>{All.toFixed(2)} ({((All-Lus)*(1-0.035)).toFixed(2)})</h6></Container>
}

function showdHeroInfos(data) {
    if (data!=null){
        let count = 0
        for (let key in data){
            count+=data[key]
        }
        return <>{count}</>
    }
    return <>0</>
}
function ShowAllShouYiL(all_statistical,HeroInfos) {
    let count = 0
    if (HeroInfos!=null){
        for (let key in HeroInfos){
            count+=HeroInfos[key]
        }
    }
    if (count==0){
        return <>-</>
    }
    //{all_Mlus:0,all_MLUSTotal:0,all_LunaRushTokenBalance:0,all_BNBBalance:0,all_Day_mlus:0}
    let a = (all_statistical.all_MLUSTotal-all_statistical.all_Mlus) * (1 - 0.035)
    let cb =count * 15
    return <>收益:{(a/cb*100-100).toFixed(2)}%</>
}

function ShowHero(data) {
    if (data!=null){
        return <div>
            {data["common"] && <small className="common">C:{data["common"]}</small>}
            {data["rare"] && <small className="rare">R:{data["rare"] }</small>}
            {data["elite"] && <small className="elite">SR:{data["elite"] }</small>}
            {data["epic"] && <small className="epic">SSR:{data["epic"] }</small>}
            {data["legendary"] && <small className="legendary">L:{data["legendary"] }</small>}
            {data["mythical"] && <small className="mythical">SL:{data["mythical"] }</small>}
            {data["divine"] && <small className="divine">SSL:{data["divine"] }</small>}
        </div>
    }
    return <>-</>
}

async function  SignLogin(index,addr)  {
    if (window.dapp.currentAccount.address.toLowerCase()!=addr.toLowerCase()){
        console.log("请先切换钱包地址到",addr)
        return
    }
    console.log(index,addr)
    let response  = await APIGET("/get_message?addr="+addr)
    if (response.status==200){
        let result = await response.json()
        switch (result.message){
            case "sign":
                let sig = await window.dapp.browserExtension.ethereum.request(
                    { method: 'personal_sign', params: [result.data,window.dapp.currentAccount.address] })
                //console.log(sig)
                response  = await APIGET("/signlogin?addr="+addr+"&sign="+sig)
                if (response.status==200){
                    let result = await response.json()
                    switch (result.message) {
                        case "ok":
                            //console.log("登录成功!")
                            window.LunaSessions.LunaSessions[index].Luna.IsLogin=true
                            window.setLunaSessions({...window.LunaSessions})
                            break
                    }
                }
                break
            case "ok":
                //console.log("重复登录")
                window.LunaSessions.LunaSessions[index].Luna.IsLogin=true
                window.setLunaSessions({...window.LunaSessions})
                break
        }
    }
}

async function StartScript(index,addr) {
    console.log("StartScript",index,addr)
    let response  = await APIGET("/action?action=start&addr="+addr)
    if (response.status==200) {
        let result = await response.json()
        if (result.message=="start" && result.data) {
            window.LunaSessions.LunaSessions[index].Luna.ISRunning = true
            window.setLunaSessions({...window.LunaSessions})
        }
    }
}

async function StopScript(index,addr) {
    console.log("StopScript",index,addr)
    let response  = await APIGET("/action?action=stop&addr="+addr)
    if (response.status==200) {
        let result = await response.json()
        if (result.message=="stop" && result.data) {
            window.LunaSessions.LunaSessions[index].Luna.ISStoping = true
            window.setLunaSessions({...window.LunaSessions})
        }
    }
}

async function ContinueScript(index,addr) {
    console.log("Continue",index,addr)
    let response  = await APIGET("/action?action=continue&addr="+addr)
    if (response.status==200) {
        let result = await response.json()
        if (result.message=="continue" && result.data) {
            window.LunaSessions.LunaSessions[index].Luna.ISStoping = false
            window.setLunaSessions({...window.LunaSessions})
        }
    }
}

async function ChangeMark(index,addr,mark) {
    console.log("ChangeMark",index,addr)
    let response  = await APIGET("/action?action=mark&addr="+addr+"&mark="+encodeURIComponent(mark))
    if (response.status==200) {
        let result = await response.json()
        if (result.message=="mark") {
            window.LunaSessions.LunaSessions[index].Mark = result.data
            window.setLunaSessions({...window.LunaSessions})

        }
    }
}

function showAction(index,addr,islogin,isrun,isstop) {
    if (islogin){
        return <ButtonGroup size="sm">
            {!isrun&&<Button variant={"warning"} onClick={()=>StartScript(index,addr)}>启动脚本</Button>}
            {(isrun&&!isstop)&&<Button variant={"danger"} onClick={()=>StopScript(index,addr)}>暂停脚本</Button>}
            {(isrun&&isstop)&&<Button variant={"success"} onClick={()=>ContinueScript(index,addr)}>继续脚本</Button>}
        </ButtonGroup>
    }
    let _disabled = !window.dapp.isBrowserExtensionEnabled || (window.dapp.currentAccount && window.dapp.currentAccount.address.toLowerCase()!=addr.toLowerCase())
    return <Button size="sm"
        onClick={()=>SignLogin(index,addr)}
        disabled={_disabled}
    >{_disabled?"连接钱包":"授权登录"}</Button>
}

function MyAccs() {
    const [LunaSessions, setLunaSessions] = React.useState({"LunaSessions":[],"AutoClaim":false})
    const [all_statistical,setall_statistical] = React.useState({all_Mlus:0,all_MLUSTotal:0,all_LunaRushTokenBalance:0,all_BNBBalance:0,all_Day_mlus:0})
    const [all_heros,setall_heros] = React.useState({})

    window.all_heros = all_heros
    window.all_statistical = all_statistical
    window.LunaSessions = LunaSessions
    window.setLunaSessions = setLunaSessions

    window.refreshLunaSessions = async function () {
        let response  = await APIGET("/get_tokenall")
        if (response.status==200){
            let result = await response.json()
            if (document.getElementById("AutoClaim")){
                document.getElementById("AutoClaim").checked = result.AutoClaim
            }
            setLunaSessions(result)
            console.log(result)
            if (result.LunaSessions){
                let _all_heros = {}
                let _all_statistical = {all_Mlus:0,all_MLUSTotal:0,all_LunaRushTokenBalance:0,all_BNBBalance:0,all_Day_mlus:0}

                for (let i = 0; i < result.LunaSessions.length; i++) {
                    let session = result.LunaSessions[i]
                    if (session.Luna){
                        for (const key in session.Luna.HeroInfos) {
                            if (_all_heros[key]==undefined){
                                _all_heros[key] = 0
                            }
                            _all_heros[key]+=session.Luna.HeroInfos[key]
                        }
                    }
                    _all_statistical["all_Mlus"] +=session.Luna.Mlus
                    _all_statistical["all_MLUSTotal"] +=session.Luna.MLUSTotal
                    _all_statistical["all_LunaRushTokenBalance"] +=session.LunaRushTokenBalance
                    _all_statistical["all_BNBBalance"] +=session.BNBBalance
                    _all_statistical["all_Day_mlus"] +=session.Luna.Day_mlus
                }
                setall_statistical({..._all_statistical})
                setall_heros({..._all_heros})
            }

        }
    }




    return <Container fluid>
        <Table striped bordered variant="light" size="sm" responsive="sm" >
            <thead>
            <tr>
                <th>#</th>
                <th>钱包信息</th>
                <th>英雄统计</th>
                <th>游戏余额</th>
                <th>钱包余额</th>
                <th>英雄总数</th>
                <th>体力统计</th>
                <th>当前关卡</th>
                <th>怪血量</th>
                <th>开始时间</th>
                <th>预计日产</th>
                <th>提现费率</th>
                <th>操作</th>
                <th>最后错误</th>
            </tr>
            </thead>
            <tbody>
            <tr className="center_td" key="all">
                <td>总</td>
                <td>{ShowAllShouYiL(all_statistical,all_heros)}</td>
                <td>{ShowHero(all_heros)}</td>
                <td>{showGameBalance(all_statistical.all_Mlus,all_statistical.all_MLUSTotal)}</td>
                <td>{showWalletBalance(all_statistical.all_LunaRushTokenBalance,all_statistical.all_BNBBalance)}</td>
                <td>{showdHeroInfos(all_heros)}</td>
                <td></td>
                <td></td>
                <td></td>
                <td></td>
                <td>≈{all_statistical.all_Day_mlus.toFixed(2)}</td>
                <td></td>
                <td></td>
                <td></td>

            </tr>
            {LunaSessions.LunaSessions && LunaSessions.LunaSessions.map((data,index)=>(
                <tr className="center_td" key={index}>
                    <td>{index+1}</td>
                    <td>{showWalletInfo(index,data.Luna.WalletAddress,data.Mark)}</td>
                    <td>{ShowHero(data.Luna.HeroInfos)}</td>
                    <td>{showGameBalance(data.Luna.Mlus,data.Luna.MLUSTotal)}</td>
                    <td>{showWalletBalance(data.LunaRushTokenBalance,data.BNBBalance)}</td>
                    <td>{showdHeroInfos(data.Luna.HeroInfos)}</td>
                    <td>{data.Luna.StaminaCount}</td>
                    <td>{data.Luna.Level}-{data.Luna.BossLevelHp}</td>
                    <td>{data.Luna.Current_HP}</td>
                    <td>{dateDiff(data.CreateTime)}</td>
                    <td>≈{data.Luna.Day_mlus.toFixed(2)}</td>
                    <td>{showFeeInfo(data.Luna.Fee,data.Luna.Last_exchange)}</td>
                    <td>{showAction(index,data.Luna.WalletAddress,data.Luna.IsLogin,data.Luna.ISRunning,data.Luna.ISStoping)}</td>
                    <td>
                        <p className="text-truncate">{data.Error}</p>
                    </td>

                </tr>
            ))}



            </tbody>
        </Table>
    </Container>
}

export default MyAccs;