import { Navbar,Nav,Button,Container,Form,FormControl} from 'react-bootstrap';
import React from "react";
import {APIGET} from "./apisrv";

async function AutoClaim() {
    let response  = await APIGET("/action?action=AutoClaim")
    if (response.status==200) {
        let result = await response.json()
        if (result.message=="AutoClaim") {
            if (document.getElementById("AutoClaim")){
                document.getElementById("AutoClaim").checked = result.data
            }
        }
    }
}
function MyNavbar(dapp) {
    let Installed_Msg = dapp.isBrowserExtensionInstalled ? "已安装钱包":"未安装钱包"
    let Enabled_msg = dapp.isBrowserExtensionEnabled ? "已连接":"激活钱包"
    return <Navbar collapseOnSelect expand="lg" bg="dark" variant="dark">
        <Container fluid>
            <Navbar.Brand href="#home">露娜-远程托管</Navbar.Brand>
            <Navbar.Toggle aria-controls="responsive-navbar-nav" />
            <Navbar.Collapse id="responsive-navbar-nav">
                <Nav className="me-auto">
                    <Nav.Link href="https://lunarush.io/" target="_blank">露娜官网</Nav.Link>
                    <Nav.Link href="https://app.lunarush.io/" target="_blank">开始游戏</Nav.Link>
                    <Nav.Link href="https://poocoin.app/tokens/0xde301d6a2569aefcfe271b9d98f318baee1d30a4" target="_blank">价格图表</Nav.Link>
                </Nav>
                <Nav>
                    <Nav.Link onClick={()=>window.refreshLunaSessions()} >刷新列表</Nav.Link>
                    <Nav.Link href="#AutoClaim"><Form>
                        <Form.Check
                            type="switch"
                            id="AutoClaim"
                            label="自动提现"
                            onClick={()=>AutoClaim()}
                        />
                    </Form></Nav.Link>
                    <Nav.Link as="p">{Installed_Msg}</Nav.Link>
                    {dapp.isBrowserExtensionInstalled &&
                    <Nav.Link onClick={() => dapp.enableBrowserExtension()} disabled={dapp.isBrowserExtensionEnabled} eventKey={2} >
                        {Enabled_msg}
                    </Nav.Link>}

                </Nav>
            </Navbar.Collapse>
        </Container>
    </Navbar>
}

export default MyNavbar;